"use client";
import { useState, useEffect } from "react";
import Header from "@/components/Header";
import ItemCard from "@/components/ItemCard";
import { Item } from "@/types"; 

export default function Home() {
  const [items, setItems] = useState<Item[]>([]);
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    const fetchItems = async () => {
      try {
        const res = await fetch("http://127.0.0.1:8000/items");
        const data = await res.json();
        
        if (Array.isArray(data)) {
          setItems(data);
        } else {
          setItems([]);
        }
      } catch (error) {
        console.error("Error fetching items:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchItems();
  }, []);

  // ‡∏ü‡∏±‡∏á‡∏Å‡πå‡∏ä‡∏±‡∏ô‡∏à‡∏≥‡∏•‡∏≠‡∏á‡∏Å‡∏≤‡∏£‡∏Å‡∏î‡πÄ‡∏ö‡∏¥‡∏Å (‡πÄ‡∏î‡∏µ‡πã‡∏¢‡∏ß‡πÄ‡∏£‡∏≤‡∏Ñ‡πà‡∏≠‡∏¢‡∏°‡∏≤‡πÄ‡∏Ç‡∏µ‡∏¢‡∏ô Logic ‡∏ï‡∏∞‡∏Å‡∏£‡πâ‡∏≤‡∏™‡∏¥‡∏ô‡∏Ñ‡πâ‡∏≤‡πÉ‡∏™‡πà‡∏ï‡∏£‡∏á‡∏ô‡∏µ‡πâ)
  const handleBorrowClick = (item: Item) => {
    console.log("User ‡∏à‡∏∞‡πÄ‡∏ö‡∏¥‡∏Å:", item.name);
    // TODO: Add to Cart logic
  };

  return (
    <div className="min-h-screen bg-gray-100 p-8">
      {/* 1. ‡πÄ‡∏£‡∏µ‡∏¢‡∏Å‡πÉ‡∏ä‡πâ Header Component */}
      <Header />

      {/* Content */}
      <main className="max-w-4xl mx-auto">
        {loading ? (
          <p className="text-center text-xl">Loading data...</p>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {/* 2. ‡∏ß‡∏ô‡∏•‡∏π‡∏õ‡πÄ‡∏£‡∏µ‡∏¢‡∏Å‡πÉ‡∏ä‡πâ ItemCard Component */}
            {items.map((item) => (
              <ItemCard 
                key={item.item_id} 
                item={item} 
                onBorrow={handleBorrowClick} 
              />
            ))}
          </div>
        )}
      </main>
    </div>
  );
}